import { useEffect, useState } from "react";
import { Interview } from "@/lib/models/candidate";
import { fetchAllContactInterviews } from "@/api/candidates/interviews";
import { useRouter } from "next/router";

export default function AllInterviews() {
  const [allInterviews, setAllInterviews] = useState<Interview[] | null>(null);
  const router = useRouter();
  const id = router.query.id;

  useEffect(() => {
    try {
      fetchAllContactInterviews().then((data) => {
        console.log(data);
        const filteredInterviews = data.filter(
          (interview: Interview) => interview.clientJob?.jobId === Number(id)
        );
        setAllInterviews(filteredInterviews);
        console.log(filteredInterviews);
      });
    } catch (err) {
      console.log(err);
    }
  }, []);

  return (
    <div>
      {allInterviews &&allInterviews?.length > 0 ? allInterviews?.map((interview: Interview) => (
        <div key={interview.interviewId}>
          <h3>{interview.interviewDate}</h3>
          <p>{interview.interviewStatus}</p>
        </div>
      )):<h1>No Interviews Found</h1>}
    </div>
  );
}
